
import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import bemCnFast from 'bem-cn-fast';

import './index.pcss';

const bemCn = bemCnFast('wt-icon');

export const Size = {
  "xs": "xs","s": "s","m": "m","l": "l"
};

const EmailIcon = React.forwardRef(
  ({ theme, size, className, ...props }, ref) => {if (size === Size['xs']) { return (<svg viewBox="0 0 16 16" {...props} className={cn(bemCn({theme, size}), className)} ref={ref}><path d="M13.25 3H2.75L8 7zM8 9L2 4.57v6.76A1.59 1.59 0 003.5 13h9a1.59 1.59 0 001.5-1.67V4.57z" /></svg>); }
return (<svg viewBox="0 0 24 24" {...props} className={cn(bemCn({theme, size}), className)} ref={ref}><path d="M12 14.5L4 8.06v8.77A2.09 2.09 0 006 19h12a2.09 2.09 0 002-2.17V8.06z" /><path d="M19.2 6H4.8l7.2 6z" /></svg>);}
);

EmailIcon.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.oneOf(["light", "dark"]),
  size: PropTypes.oneOf(["xs", "s", "m", "l"])
};

EmailIcon.defaultProps = {
  size: "m"
};

export default EmailIcon;