import React, { useEffect, useState, useImperativeHandle, useRef } from 'react';
import PropTypes from 'prop-types';
import bemCnFast from 'bem-cn-fast';
import { Manager, Reference, Popper } from 'react-popper';
import {
  LayeringConsumer,
  LayeringProvider,
  withTheme
} from '@webteam/ui-contexts';
import ReactDOM from 'react-dom';

import './styles';

const bemCn = bemCnFast('wt-dropdown');

const Portal = ({ children, target = document.body }) =>
  ReactDOM.createPortal(children, target);
/* eslint-disable react/prop-types */
function DropdownRender(
  {
    children,
    trigger,
    size = 'm',
    theme = 'light',
    /* @private */
    borderHardness = 'average',
    isOpen = false,
    placement = 'bottom-start',
    onRequestClose = () => {},
    /* @private */
    widthEqualsTriggers = false,
    /* @private */
    watchWindowResize = false
  },
  externalRef
) {
  const [popperRef, setPopperRef] = useState(null);
  const [triggerRef, setTriggerRef] = useState(null);

  const [minWidth, setMinWidth] = useState(undefined);

  const handleDocumentClick = e => {
    if (!popperRef || e.target === popperRef || popperRef.contains(e.target)) {
      return;
    }

    if (
      !triggerRef ||
      e.target === triggerRef ||
      triggerRef.contains(e.target)
    ) {
      return;
    }
    onRequestClose();
  };

  const handleWindowResize = () => {
    if (watchWindowResize && widthEqualsTriggers && triggerRef) {
      setMinWidth(triggerRef.getBoundingClientRect().width);
    }
  };

  // eslint-disable-next-line complexity
  useEffect(() => {
    if (!(widthEqualsTriggers && triggerRef)) {
      return;
    }

    setMinWidth(triggerRef.getBoundingClientRect().width);

    const shouldWatchResize =
      isOpen &&
      popperRef &&
      triggerRef &&
      widthEqualsTriggers &&
      watchWindowResize;

    if (!shouldWatchResize) {
      return;
    }

    window.addEventListener('resize', handleWindowResize, {
      capture: true,
      passive: true
    });

    // eslint-disable-next-line consistent-return
    return () => {
      window.removeEventListener('resize', handleWindowResize, true);
    };
  }, [
    isOpen,
    popperRef,
    triggerRef,
    widthEqualsTriggers,
    watchWindowResize,
    handleWindowResize
  ]);

  // eslint-disable-next-line consistent-return
  useEffect(() => {
    if (isOpen && popperRef) {
      document.addEventListener('click', handleDocumentClick, true);
      return () =>
        document.removeEventListener('click', handleDocumentClick, true);
    }
  }, [handleDocumentClick, isOpen, popperRef]);

  const createTriggerRefHandler = popperHandleRef => triggerReference => {
    setTriggerRef(triggerReference);
    popperHandleRef(triggerReference);
  };

  const triggerRenderer =
    typeof trigger === 'function'
      ? ({ ref, ...rest }, ...restArgs) =>
          trigger({ ref: createTriggerRefHandler(ref), ...rest }, ...restArgs)
      : function TriggerWrapper({ ref }) {
          return (
            <span
              data-test="dropdown-trigger"
              className={bemCn('trigger-wrapper')}
              ref={createTriggerRefHandler(ref)}
            >
              {trigger}
            </span>
          );
        };

  const scheduleUpdateRef = useRef(null);
  useImperativeHandle(externalRef, () => ({
    scheduleUpdate: () => {
      if (scheduleUpdateRef.current) {
        scheduleUpdateRef.current();
      }
    }
  }));

  /* eslint-disable react/jsx-no-bind */
  return (
    <Manager>
      <Reference>{triggerRenderer}</Reference>
      {isOpen && (
        <Portal>
          <LayeringConsumer>
            {({ dropdownZIndex }) => (
              <Popper
                placement={placement}
                modifiers={{
                  hide: { enabled: false },
                  preventOverflow: { enabled: false },
                  keepTogether: { enabled: true },
                  flip: { enabled: true, flipVariations: true }
                }}
              >
                {({ ref, style, scheduleUpdate }) => {
                  scheduleUpdateRef.current = scheduleUpdate;
                  return (
                    <div
                      ref={pRef => {
                        setPopperRef(pRef);
                        ref(pRef);
                      }}
                      style={{
                        ...style,
                        minWidth,
                        maxWidth: widthEqualsTriggers ? 'none' : undefined,
                        zIndex: dropdownZIndex
                      }}
                      className={bemCn({
                        size,
                        theme,
                        'border-hardness': borderHardness
                      })}
                    >
                      <LayeringProvider baseLayer={'dropdown'}>
                        {children}
                      </LayeringProvider>
                    </div>
                  );
                }}
              </Popper>
            )}
          </LayeringConsumer>
        </Portal>
      )}
    </Manager>
  );
}
/* eslint-enable react/prop-types */

const Dropdown = React.forwardRef(DropdownRender);

Dropdown.propTypes = {
  trigger: PropTypes.oneOfType([PropTypes.element, PropTypes.func]).isRequired,
  isOpen: PropTypes.bool.isRequired,
  onRequestClose: PropTypes.func,
  size: PropTypes.oneOf(['m', 's', 'xs']),
  theme: PropTypes.oneOf(['light', 'dark']),
  borderHardness: PropTypes.oneOf(['average', 'pale']),
  placement: PropTypes.oneOf([
    'auto-start',
    'auto',
    'auto-end',
    'top-start',
    'top',
    'top-end',
    'right-start',
    'right',
    'right-end',
    'bottom-end',
    'bottom',
    'bottom-start',
    'left-end',
    'left',
    'left-start'
  ])
};

export default withTheme(Dropdown);
