import React, { useRef, useState } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import bemCnFast from 'bem-cn-fast';

import { withTheme } from '@webteam/ui-contexts';
import { DownIcon, CloseIcon, LoadingIcon } from '@webteam/icons';

import './trigger.pcss';

const bemCn = bemCnFast('wt-select');

const TIMEOUT_BETWEEN_EVENTS = 100; //100ms

function SelectTrigger(props) {
  const {
    size,
    theme,
    className,
    triggerRef,
    isOpen,
    text,
    placeholder = '',
    isClearable,
    isLoading,
    isDisabled,
    searchInput,
    isSearchable,
    onClearClick,
    onSearchChange,
    error,
    ...restProps
  } = props;

  const inputRef = useRef(null);
  const [searchFocused, setSearchFocused] = useState(false);
  const [lastMouseDownTimestamp, setLastMouseDownTimestamp] = useState(0);

  function handleChange(event) {
    const newValue = event.target.value;
    onSearchChange(newValue);
  }

  function handleBlur() {
    if (isSearchable && inputRef.current) {
      setSearchFocused(false);
      inputRef.current.blur();
    }
  }

  function handleFocus(e) {
    if (
      e.timeStamp - lastMouseDownTimestamp > TIMEOUT_BETWEEN_EVENTS &&
      isSearchable &&
      inputRef.current
    ) {
      setSearchFocused(true);
      inputRef.current.focus();
    }
  }

  function handleMouseDown(e) {
    setLastMouseDownTimestamp(e.timeStamp);
  }

  return (
    <div
      tabIndex={isSearchable ? -1 : 0}
      className={cn(
        bemCn({
          size,
          theme,
          searchFocused,
          open: isOpen,
          disabled: isDisabled,
          error: Boolean(error)
        }),
        className
      )}
      ref={triggerRef}
      {...restProps}
    >
      <div className={bemCn('label', { notSelected: !text })}>
        {searchInput === '' ? text || placeholder : ''}
        {isSearchable && !isDisabled && (
          <input
            ref={inputRef}
            tabIndex={isSearchable ? 0 : -1}
            onBlur={handleBlur}
            onFocus={handleFocus}
            onMouseDown={handleMouseDown}
            className={bemCn('search-input', { size })}
            autoCapitalize="none"
            autoComplete="off"
            autoCorrect="off"
            spellCheck="false"
            type="text"
            value={searchInput}
            onChange={handleChange}
            data-test="search-input"
          />
        )}
      </div>
      <div className={bemCn('icons-wrapper', { size })}>
        {renderClearOrLoading(props)}
        <DownIcon size={size} className={bemCn('toggle', { open: isOpen })} />
      </div>
    </div>
  );
}

function renderClearOrLoading(props) {
  const {
    isClearable,
    isLoading,
    isDisabled,
    onClearClick,
    text,
    size
  } = props;

  if (isLoading) {
    return (
      <LoadingIcon
        style={{ pointerEvents: 'none' }}
        size={size}
        className={bemCn('icon')}
        data-test="loading-icon"
      />
    );
  } else if (isClearable && !isDisabled && !isNullOrEmptyString(text)) {
    return (
      <CloseIcon
        size={size}
        className={bemCn('icon')}
        data-test="clear-icon"
        onClick={e => {
          e.stopPropagation();
          onClearClick();
        }}
      />
    );
  } else {
    return null;
  }
}

function isNullOrEmptyString(str) {
  if (typeof str === 'undefined' || str == null) {
    return true;
  }

  return str.length === 0;
}

SelectTrigger.propTypes = {
  size: PropTypes.oneOf(['m', 's', 'xs']),
  theme: PropTypes.oneOf(['light', 'dark']),
  className: PropTypes.string,
  isOpen: PropTypes.bool,
  text: PropTypes.string,
  placeholder: PropTypes.string,
  isClearable: PropTypes.bool,
  isLoading: PropTypes.bool,
  isDisabled: PropTypes.bool,
  isSearchable: PropTypes.bool,
  onClearClick: PropTypes.func,
  onSearchChange: PropTypes.func,
  error: PropTypes.bool,
  triggerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any
    })
  ]),
  searchInput: PropTypes.string
};

export default withTheme(SelectTrigger);
