import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { LoadingIcon } from '@webteam/icons';

import bemCnFast from 'bem-cn-fast';
import cn from 'classnames';
import { withTheme } from '@webteam/ui-contexts';

// Import CSS directly because there is alternative input implementation for use as CSS API
import './index.pcss';

const bemCn = bemCnFast('wt-input');

export class Input extends PureComponent {
  static propTypes = {
    id: PropTypes.string,
    /** Set input ClassName */
    className: PropTypes.string,
    /**  */
    placeholder: PropTypes.string,
    /** Disable input */
    disabled: PropTypes.bool,
    /** Error state flag or message */
    error: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),
    /** Set busy state */
    busy: PropTypes.bool,
    /** Change value callback. `value => ...` */
    onChange: PropTypes.func,
    /** Focus input callback */
    onFocus: PropTypes.func,
    /** Blur input callback */
    onBlur: PropTypes.func,
    /** Size */
    size: PropTypes.oneOf(['xs', 's', 'm', 'l']),
    /** Appearance theme */
    theme: PropTypes.oneOf(['light', 'dark']),
    /** Change input behavior */
    type: PropTypes.oneOf([
      'text',
      'email',
      'search',
      'date',
      'password',
      'number',
      'file'
    ]),
    /** Set value to control state of the input. `onChange` callback is required. */
    value: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
      PropTypes.array
    ]),
    icon: PropTypes.node,
    label: PropTypes.node,
    note: PropTypes.node
  };

  static defaultProps = {
    theme: 'light',
    size: 'm',
    type: 'text'
  };

  constructor(props) {
    super(props);
    this.state = { empty: !props.value };
  }

  componentWillUnmount() {
    const { onChange, type } = this.props;
    if (type === 'file' && onChange) {
      onChange({
        target: {
          type: 'file',
          files: []
        }
      });
    }
  }

  handleInputRef = inputRef => {
    this.inputRef = inputRef;
  };

  handleChange = e => {
    const { onChange } = this.props;
    this.setState({ empty: !e.target.value });
    if (onChange) {
      onChange(e);
    }
  };

  focus = () => {
    this.inputRef.focus();
  };

  renderIcon = () => {
    const { icon, busy, size, theme } = this.props;

    if (busy) {
      return (
        <LoadingIcon size={size} className={'wt-input__icon'} theme={theme} />
      );
    }

    if (icon) {
      return React.cloneElement(icon, {
        size,
        theme,
        className: cn(icon.props.className, 'wt-input__icon')
      });
    }

    return null;
  };

  // eslint-disable-next-line complexity
  render() {
    const {
      id,
      className,
      disabled,
      error,
      type,
      theme,
      size,
      icon,
      busy,
      label,
      note,
      // eslint-disable-next-line react/prop-types
      style,
      ...restProps
    } = this.props;
    const { empty } = this.state;

    if (type === 'file' && restProps.value !== null) {
      delete restProps.value;
    }

    let labelTextClass =
      // eslint-disable-next-line no-nested-ternary
      size === 'xs' ? 'wt-text-3' : size === 'l' ? 'wt-text-1' : 'wt-text-2';

    let errorAndNoteTextClass = size === 'l' ? 'wt-text-2' : 'wt-text-3';

    if (theme === 'dark') {
      errorAndNoteTextClass = `${errorAndNoteTextClass} ${errorAndNoteTextClass}_theme_dark`;
      labelTextClass = `${labelTextClass} ${labelTextClass}_theme_dark`;
    }

    const hasIcon = !!icon || busy;
    return (
      <label
        className={cn(
          bemCn({
            theme,
            size,
            error: Boolean(error),
            empty,
            icon: hasIcon
          }),
          className
        )}
        style={style}
        id={id}
      >
        {label && <div className={labelTextClass}>{label}</div>}
        <div className={bemCn('wrapper')}>
          <input
            {...restProps}
            className={cn(bemCn('inner'))}
            type={type}
            disabled={disabled}
            onChange={this.handleChange}
            ref={this.handleInputRef}
            aria-invalid={Boolean(error)}
          />
          {this.renderIcon()}
        </div>
        {note && (
          <div className={cn(bemCn('note'), errorAndNoteTextClass)}>{note}</div>
        )}
        {error && typeof error !== 'boolean' && (
          <div className={cn(bemCn('error-message'), errorAndNoteTextClass)}>
            {error}
          </div>
        )}
      </label>
    );
  }
}

export default withTheme(Input);
