import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  VKShareButton,
  RedditShareButton,
  PocketShareButton,
  EmailShareButton
} from 'react-share';
import {
  FacebookIcon,
  LinkedinIcon,
  TwitterIcon,
  VkIcon,
  RedditIcon,
  PocketIcon,
  EmailIcon
} from '@webteam/icons';
import { useEffect, useState } from 'react';

const MAX_TEXT_LENGTH = 243;

export const SOCIAL_NETWORKS = {
  facebook: `facebook`,
  linkedin: `linkedin`,
  twitter: `twitter`,
  vk: `vk`,
  reddit: `reddit`,
  pocket: `pocket`,
  email: `email`
};

export const SOCIAL_COMPONENTS = {
  [SOCIAL_NETWORKS.facebook]: FacebookShareButton,
  [SOCIAL_NETWORKS.linkedin]: LinkedinShareButton,
  [SOCIAL_NETWORKS.twitter]: TwitterShareButton,
  [SOCIAL_NETWORKS.vk]: VKShareButton,
  [SOCIAL_NETWORKS.reddit]: RedditShareButton,
  [SOCIAL_NETWORKS.pocket]: PocketShareButton,
  [SOCIAL_NETWORKS.email]: EmailShareButton
};

export const SOCIAL_ICONS = {
  [SOCIAL_NETWORKS.facebook]: FacebookIcon,
  [SOCIAL_NETWORKS.linkedin]: LinkedinIcon,
  [SOCIAL_NETWORKS.twitter]: TwitterIcon,
  [SOCIAL_NETWORKS.vk]: VkIcon,
  [SOCIAL_NETWORKS.reddit]: RedditIcon,
  [SOCIAL_NETWORKS.pocket]: PocketIcon,
  [SOCIAL_NETWORKS.email]: EmailIcon
};

const getFullText = (text, hashtag) =>
  text.length >= MAX_TEXT_LENGTH
    ? `${text.slice(0, MAX_TEXT_LENGTH - (hashtag ? hashtag.length : 0))}...`
    : text;

export const adaptPropsToShareButton = (network, options) => {
  const { hashtag = '', text, url, via } = options;
  const settings = {
    url
  };
  let fullText = getFullText(text, hashtag);

  if (hashtag) {
    fullText += ` #${hashtag}`;
  }

  switch (network) {
    case SOCIAL_NETWORKS.facebook:
      settings.quote = fullText;
      break;
    case SOCIAL_NETWORKS.twitter:
      settings.title = fullText;
      if (via) settings.via = via;
      break;
    case SOCIAL_NETWORKS.email:
      settings.subject = fullText;
      break;
    default:
      settings.title = fullText;
  }

  return settings;
};

export const shareTracking = (network, url) => {
  if (typeof window !== `undefined`) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      event: 'GASocial',
      socialNetwork: network,
      socialAction: 'share',
      socialTarget: url
    });
  }
};

const getPageUrl = () =>
  typeof window !== `undefined` ? window.location.href : '';

const getPageTitle = () =>
  typeof window !== `undefined` && document.querySelector('title')
    ? document.querySelector('title').textContent
    : '';

export const usePageOptions = (propsUrl, propsText) => {
  const [url, setUrl] = useState(propsUrl || '');
  const [text, setText] = useState(propsText || '');

  useEffect(() => {
    if (!propsUrl) {
      setUrl(getPageUrl());
    } else {
      setUrl(propsUrl);
    }
  }, [propsUrl]);

  useEffect(() => {
    if (!propsText) {
      setText(getPageTitle());
    } else {
      setText(propsText);
    }
  }, [propsText]);

  return { url, text };
};
