import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import bemCnFast from 'bem-cn-fast';
import Button from '@webteam/button';
import { ShareIcon, CloseIcon } from '@webteam/icons';
import { withTheme } from '@webteam/ui-contexts';

import SocialButtons from './parts/social-buttons';

import { SOCIAL_NETWORKS, shareTracking, usePageOptions } from './utils';
import './styles';

const DEFAULT_NETWORKS = [
  SOCIAL_NETWORKS.twitter,
  SOCIAL_NETWORKS.facebook,
  SOCIAL_NETWORKS.linkedin,
  SOCIAL_NETWORKS.reddit,
  SOCIAL_NETWORKS.email
];
const BASE_BEM_CLASSNAME = 'wt-social-share';
const HIDE_TIMEOUT = 250;

const SocialShare = ({
  className,
  isCollapsed = true,
  theme = 'light',
  tracking = true,
  networks,
  size = 'm',
  url,
  text,
  ...restOptions
}) => {
  const mainOptions = usePageOptions(url, text);
  const [isShareClicked, setIsShareClicked] = useState(false);
  const [isShareHovered, setIsShareHovered] = useState(false);
  const [isHovered, setIsHovered] = useState(false);
  const [isHoveredTimeout, setIsHoveredTimeout] = useState(false);

  const checkIsOpen = () =>
    isHovered || isShareClicked || !isCollapsed || isHoveredTimeout;
  const isOpen = checkIsOpen();

  const bemCn = bemCnFast(BASE_BEM_CLASSNAME);

  useEffect(() => {
    const checkTimeout = setTimeout(() => {
      if (isHoveredTimeout) {
        setIsHoveredTimeout(false);
      }
      return null;
    }, HIDE_TIMEOUT);

    return () => clearTimeout(checkTimeout);
  }, [isHoveredTimeout]);

  const shareButtonMouseEnterHandler = () => {
    if (!isShareHovered) {
      setIsHovered(true);
      setIsShareHovered(true);
    }
  };
  const shareButtonMouseLeaveHandler = () => setIsShareHovered(false);
  const shareButtonClickHandler = () => {
    setIsShareClicked(!isShareClicked);
    setIsHovered(false);
  };
  const socialButtonClickHandler = (network, pageUrl) => {
    setIsShareClicked(false);
    if (tracking) {
      shareTracking(network, pageUrl);
    }
  };
  const buttonsMouseLeaveHandler = () => {
    if (isCollapsed) {
      if (!isShareClicked && isOpen) {
        setIsHoveredTimeout(true);
      }
      setIsHovered(false);
    }
  };
  const buttonsMouseEnterHandler = () =>
    isHoveredTimeout && isCollapsed && setIsHovered(true);

  return (
    <div
      className={cn(bemCn(), className)}
      onMouseLeave={buttonsMouseLeaveHandler}
      onMouseEnter={buttonsMouseEnterHandler}
    >
      <div className={bemCn('wrapper', { size })}>
        {isCollapsed && (
          <div className={bemCn('item', { size })}>
            <Button
              mode="nude"
              size={size}
              theme={theme}
              icon={isShareClicked ? <CloseIcon /> : <ShareIcon />}
              onMouseEnter={shareButtonMouseEnterHandler}
              onMouseLeave={shareButtonMouseLeaveHandler}
              onClick={shareButtonClickHandler}
            />
          </div>
        )}

        {isOpen && (
          <SocialButtons
            className={BASE_BEM_CLASSNAME}
            theme={theme}
            size={size}
            networks={networks || DEFAULT_NETWORKS}
            options={{ ...mainOptions, ...restOptions }}
            clickHandler={socialButtonClickHandler}
          />
        )}
      </div>
    </div>
  );
};

SocialShare.propTypes = {
  /** Will be added after the text */
  hashtag: PropTypes.string,
  /**
   * Array of (one of ['facebook' | 'linkedin' | 'twitter' | 'vk' | 'reddit' | 'pocket' | 'email'])
   * __By default: ['twitter', 'facebook', linkedin', 'reddit', 'email']__
   */
  networks: PropTypes.arrayOf(PropTypes.oneOf(Object.values(SOCIAL_NETWORKS))),
  /** Text for sharing, __max length 240 characters__, by default - title of current page */
  text: PropTypes.string,
  /** Push the tracking values to the dataLayer after click on share buttons */
  tracking: PropTypes.bool,
  /** Url for sharing, by default - url of current page */
  url: PropTypes.string,
  /** Used for twitter, adds "via @{via-string}" after the url */
  via: PropTypes.string,
  /** Additional class-name */
  className: PropTypes.string,
  theme: PropTypes.oneOf(['light', 'dark']),
  /**
   * 'm' recommended to use for mobile,
   * 's' recommended to use in footer (without collapse),
   * 'xs' recommended to use with charts
   */
  size: PropTypes.oneOf(['m', 's', 'xs']),
  /** Show all buttons without "Share" button */
  isCollapsed: PropTypes.bool
};

export default withTheme(SocialShare);
