import React, {
  useState,
  useReducer,
  useEffect,
  useRef,
  useCallback
} from 'react';
import PropTypes from 'prop-types';
import scrollIntoView from 'scroll-into-view-if-needed';

// eslint-disable-next-line import/order
import Dropdown from '@webteam/dropdown';
// eslint-disable-next-line import/order
import { List, ListItem, Divider, GroupHeader } from '@webteam/list';

import { withTheme } from '@webteam/ui-contexts';

import SelectTrigger from './parts/trigger';
import FormikTriggerWrapper from './parts/formik-trigger-wrapper';

import { getInitialState, reducer } from './state/reducer';

import {
  cursorMovedUp,
  cursorMovedDown,
  setCursorIndex,
  setGroups,
  setSelectedIndex,
  clearSelectedAndCloseMenu,
  setSearchString,
  openMenu,
  closeMenu
} from './state/actions';

import {
  withoutGroupsLabel,
  getCursorIndex,
  isEmpty,
  getPlainOptions,
  getSelectedValue,
  getSelectedLabel,
  getOptionValue,
  OptionGroupTypes,
  isIndexMatchCursor
} from './state/selectors';

const ARROW_UP = 'ArrowUp';
const ARROW_DOWN = 'ArrowDown';
const SPACE = ' ';
const ESCAPE = 'Escape';
const ENTER = 'Enter';
const TAB = 'Tab';
const BACKSPACE = 'Backspace';

const DEFAULT_NO_OPTIONS_MESSAGES = {
  noOptions: 'No options',
  nothingFound: 'Nothing found',
  loading: 'Loading...'
};

// eslint-disable-next-line complexity
function Select({
  options,
  size = 'm',
  theme,
  className = null,
  defaultValue = null,
  value = undefined,
  onChange,
  placeholder = 'Placeholder',
  isClearable = false,
  isLoading = false,
  isDisabled = false,
  isSearchable = false,
  noOptionsMessage,
  ...restProps
}) {
  const dropdownRef = useRef(null);
  const highlightedItemRef = React.createRef();

  const [isKeyboardNavigating, setIsKeyboardNavigating] = useState(false);
  const [state, dispatch] = useReducer(
    reducer,
    getInitialState(convertToOptionGroupsIfNeeded(options), defaultValue)
  );

  useEffect(() => {
    dispatch(setGroups(convertToOptionGroupsIfNeeded(options)));
  }, [options]);

  const displayedText = value ? value.label : getSelectedLabel(state);
  const isEmptyList = isEmpty(state);

  const listItems = renderOptionList(
    state,
    size,
    highlightedItemRef,
    handleSelectIndex,
    i => {
      if (!isIndexMatchCursor(state, i)) {
        dispatch(setCursorIndex(i));
      }
    },
    size
  );

  const preventHoverIfNeeded = isKeyboardNavigating
    ? { pointerEvents: 'none' }
    : {};

  useEffect(() => {
    if (dropdownRef.current) {
      dropdownRef.current.scheduleUpdate();
    }
  }, [state.searchString]);

  const handleMouseMove = useCallback(() => setIsKeyboardNavigating(false), []);

  useEffect(() => {
    if (isKeyboardNavigating) {
      document.addEventListener('mousemove', handleMouseMove);
    }

    return () => document.removeEventListener('mousemove', handleMouseMove);
  }, [handleMouseMove, isKeyboardNavigating]);

  useEffect(() => {
    if (isKeyboardNavigating && highlightedItemRef.current) {
      let elementToScrollTo = highlightedItemRef.current;

      if (state.cursorIndex[0] === 0 && state.cursorIndex[1] === 0) {
        const groupHeader = highlightedItemRef.current.previousSibling;
        if (groupHeader) {
          elementToScrollTo = groupHeader;
        }
      }

      scrollIntoView(elementToScrollTo, {
        behavior: 'instant',
        scrollMode: 'if-needed',
        block: 'nearest',
        inline: 'nearest'
      });
    }
  }, [highlightedItemRef, state.cursorIndex, isKeyboardNavigating]);

  function handleClose() {
    dispatch(closeMenu());
    setIsKeyboardNavigating(false);
  }

  function handleSelectIndex(index) {
    const selectedOption = state.groups[index[0]].options[index[1]];
    dispatch(setSelectedIndex(index));
    setIsKeyboardNavigating(false);
    if (onChange) {
      onChange(selectedOption);
    }
  }

  // eslint-disable-next-line complexity
  function handleTriggerKeyDown(e) {
    if (e.key === ESCAPE) {
      handleClose();
      e.preventDefault();
    }

    if (isDisabled) {
      return;
    }

    if (state.menuIsOpen) {
      handleKeyNavigation(e);
    } else {
      if (shouldOpenSelect(e.key)) {
        dispatch(openMenu());
        e.preventDefault();
      }

      if (e.key === BACKSPACE && isClearable) {
        handleClearClick();
        e.preventDefault();
      }
    }
  }

  // eslint-disable-next-line complexity
  function handleKeyNavigation(e) {
    if (e.key === ARROW_DOWN || e.key === ARROW_UP) {
      const isDownDirection = e.key === ARROW_DOWN;
      dispatch(isDownDirection ? cursorMovedDown() : cursorMovedUp());
      setIsKeyboardNavigating(true);
      e.preventDefault();
    }

    if (
      e.key === ENTER ||
      e.key === TAB ||
      (!isSearchable && e.key === SPACE)
    ) {
      const cursorIndex = getCursorIndex(state);
      if (cursorIndex !== null) {
        handleSelectIndex(cursorIndex);
      } else {
        handleClose();
      }
      e.preventDefault();
    }
  }

  function handleClick() {
    if (isDisabled) {
      return;
    }

    const action = state.menuIsOpen ? closeMenu() : openMenu();
    dispatch(action);
  }

  function handleSearchInputChange(searchValue) {
    const filter = new RegExp(`\\b${escapeRegex(searchValue)}`, 'i');
    dispatch(setSearchString(searchValue, ({ label }) => filter.test(label)));
  }

  function handleClearClick() {
    dispatch(clearSelectedAndCloseMenu());

    if (onChange) {
      onChange(null);
    }
  }

  return (
    <Dropdown
      size={size}
      theme={theme}
      onRequestClose={handleClose}
      borderHardness={isEmptyList ? 'pale' : 'average'}
      widthEqualsTriggers
      watchWindowResize
      ref={dropdownRef}
      trigger={({ ref }) => (
        <FormikTriggerWrapper
          className={className}
          theme={theme}
          size={size}
          error={restProps.error}
          label={restProps.label}
          note={restProps.note}
        >
          <SelectTrigger
            theme={theme}
            size={size}
            isOpen={state.menuIsOpen}
            triggerRef={ref}
            onKeyDown={handleTriggerKeyDown}
            text={displayedText}
            placeholder={placeholder}
            isClearable={isClearable}
            isLoading={isLoading}
            isDisabled={isDisabled}
            searchInput={state.searchString}
            isSearchable={isSearchable}
            onSearchChange={handleSearchInputChange}
            onClick={handleClick}
            onClearClick={handleClearClick}
            error={Boolean(restProps.error)}
          />
        </FormikTriggerWrapper>
      )}
      isOpen={state.menuIsOpen}
    >
      <List size={size} theme={theme} style={preventHoverIfNeeded}>
        {!isEmptyList ? (
          listItems
        ) : (
          <ListItem disabled>
            {getEmptyMessage(
              isLoading,
              isSearchable && state.searchString.length > 0,
              noOptionsMessage
            )}
          </ListItem>
        )}
      </List>
    </Dropdown>
  );
}

const optionType = PropTypes.shape({
  label: PropTypes.string.isRequired,
  value: PropTypes.string
});
const optionGroupType = PropTypes.shape({
  label: PropTypes.string,
  options: PropTypes.arrayOf(optionType)
});

const noOptionsMessageConfigType = PropTypes.shape({
  noOptions: PropTypes.string.isRequired,
  nothingFound: PropTypes.string,
  loading: PropTypes.string
});
const noOptionsMessageType = PropTypes.oneOf([
  PropTypes.string,
  noOptionsMessageConfigType
]);

Select.propTypes = {
  className: PropTypes.string,
  options: PropTypes.oneOfType([
    PropTypes.arrayOf(optionType),
    PropTypes.arrayOf(optionGroupType)
  ]),
  defaultValue: optionType,
  value: optionType,
  onChange: PropTypes.func,
  size: PropTypes.oneOf(['m', 's', 'xs']),
  theme: PropTypes.oneOf(['light', 'dark']),
  placeholder: PropTypes.string,
  isClearable: PropTypes.bool,
  isSearchable: PropTypes.bool,
  isDisabled: PropTypes.bool,
  isLoading: PropTypes.bool,
  error: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),
  label: PropTypes.node,
  note: PropTypes.node,
  noOptionsMessage: noOptionsMessageType
};

function renderOptionList(
  state,
  size,
  highlightedItemRef,
  handleSelectIndex,
  handleHighlightIndex
) {
  return getPlainOptions(state).map((item, i, collection) => {
    const marginInPx = withoutGroupsLabel(state)
      ? 0
      : { m: 12, s: 6, xs: 0 }[size];

    if (item.type === OptionGroupTypes.OPTION) {
      const { index, label } = item;
      const value = getOptionValue(item);
      const isSelected = value === getSelectedValue(state);
      const isHighlighted = isIndexMatchCursor(state, index);
      const lastItem = i === collection.length - 1;

      return (
        <ListItem
          key={`${value}_${index[0]}`}
          selected={isSelected}
          highlighted={isHighlighted}
          ref={isHighlighted ? highlightedItemRef : null}
          onMouseMove={() => handleHighlightIndex(index)}
          onClick={() => handleSelectIndex(index)}
          style={{ marginBottom: lastItem ? `${marginInPx}px` : '' }}
        >
          {label}
        </ListItem>
      );
    }

    if (item.type === OptionGroupTypes.DIVIDER) {
      return (
        // eslint-disable-next-line
        <Divider key={`divider${i}`} style={{ marginTop: `${marginInPx}px` }} />
      );
    }

    if (item.type === OptionGroupTypes.HEADER) {
      const { label } = item;
      // eslint-disable-next-line
      return <GroupHeader key={`header${i}`} text={label} />;
    }

    return null;
  });
}

function escapeRegex(str) {
  return str.replace(/[\-\[\]\/{}()\*+?.\\^$|]/g, '\\$&');
}

function shouldOpenSelect(key) {
  return (
    key === ARROW_DOWN || key === ARROW_UP || key === SPACE || key === ENTER
  );
}

function isString(s) {
  return Object.prototype.toString.call(s) === '[object String]';
}

function getEmptyMessage(isLoading, isNothingFound, noOptionsMessage) {
  const { noOptions, nothingFound, loading } = isString(noOptionsMessage)
    ? { ...DEFAULT_NO_OPTIONS_MESSAGES, noOptions: noOptionsMessage }
    : { ...DEFAULT_NO_OPTIONS_MESSAGES, ...noOptionsMessage };

  if (isLoading) {
    return loading;
  } else if (isNothingFound) {
    return nothingFound;
  } else {
    return noOptions;
  }
}

function convertToOptionGroupsIfNeeded(options = []) {
  if (containsGroups(options)) {
    return options;
  } else {
    return [{ options }];
  }
}

function containsGroups(opts) {
  return opts.length > 0 && opts[0].options && Array.isArray(opts[0].options);
}

export default withTheme(Select);
