import React from 'react';
import PropTypes from 'prop-types';
import bemCnFast from 'bem-cn-fast';
import cn from 'classnames';
import { withTheme } from '@webteam/ui-contexts';
import { CloseIcon } from '@webteam/icons';

import './styles';

const bemCn = bemCnFast('wt-tag');

export const Tag = React.forwardRef(function Tag(
  {
    className,
    isRemovable,
    isClickable,
    disabled,
    size = 'm',
    theme,
    htmlTag: HtmlTag = 'span',
    children,
    ...restProps
  },
  ref
) {
  return (
    <HtmlTag
      {...restProps}
      disabled={disabled}
      className={cn(
        bemCn({
          removable: isRemovable,
          size,
          theme,
          disabled,
          clickable:
            HtmlTag === 'a' || !!restProps.onClick || isRemovable || isClickable
        }),
        className
      )}
      ref={ref}
    >
      {children}
      {isRemovable && ['xs', 's'].includes(size) && !disabled && (
        <CloseIcon className={bemCn('close')} size={size} />
      )}
    </HtmlTag>
  );
});

Tag.propTypes = {
  isRemovable: PropTypes.bool,
  isClickable: PropTypes.bool,
  disabled: PropTypes.bool,
  size: PropTypes.oneOf(['xs', 's', 'm']),
  theme: PropTypes.oneOf(['dark', 'light']),
  htmlTag: PropTypes.string
};

export default withTheme(Tag);
