import './native-select.pcss';

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import bemCnFast from 'bem-cn-fast';
import { withTheme } from '@webteam/ui-contexts';

import SelectTrigger from './trigger';
import { wrapWithFormikIfNeeded } from './formik-trigger-wrapper';

const bemCn = bemCnFast('wt-native-select');

const DEFAULT_NO_OPTIONS_MESSAGES = {
  noOptions: 'No options',
  loading: 'Loading...'
};

function NativeSelect({
  options = [],
  size = 'm',
  theme,
  className = null,
  defaultValue = { label: '' },
  value = undefined,
  onChange,
  placeholder = 'Placeholder',
  isLoading = false,
  isDisabled = false,
  trigger,
  label,
  note,
  error,
  noOptionsMessage
}) {
  const [selected, setSelected] = useState(defaultValue);
  const displayed = value ? value : selected;
  const [hovered, setHovered] = useState(false);

  function handleChange(event) {
    const newValue = event.target.value;
    const selectedOption = findSelectedOption(options, newValue);
    setSelected(selectedOption);

    if (onChange) {
      onChange(selectedOption);
    }
  }

  function getNoOptionsMessage() {
    const { noOptions, loading } = isString(noOptionsMessage)
      ? { ...DEFAULT_NO_OPTIONS_MESSAGES, noOptions: noOptionsMessage }
      : { ...DEFAULT_NO_OPTIONS_MESSAGES, ...noOptionsMessage };

    if (isLoading) {
      return loading;
    } else {
      return noOptions;
    }
  }

  const placeholderOption = {
    value: '__placeholder__',
    label: options.length > 0 ? placeholder : getNoOptionsMessage(),
    disabled: true
  };

  let optionList;
  if (containsGroups(options)) {
    optionList = options.map(renderOptionGroup);
  } else {
    optionList = options.map(renderOption);
  }

  optionList.unshift(renderOption(placeholderOption));

  let _trigger;
  if (trigger) {
    _trigger = trigger({
      selectedLabel: displayed.label,
      selectedValue: displayed.value,
      placeholder,
      hovered
    });
  } else {
    _trigger = (
      <SelectTrigger
        theme={theme}
        size={size}
        isOpen={false}
        onKeyDown={() => false}
        text={displayed.label}
        placeholder={placeholder}
        searchInput=""
        isClearable={false}
        isLoading={isLoading}
        isDisabled={isDisabled}
        error={error}
      />
    );
  }
  // fragment added for react-docgen parser, otherwise this function doesn't recognized as component
  return (
    <>
      {wrapWithFormikIfNeeded(
        <div
          className={cn(bemCn(), className)}
          onMouseEnter={() => setHovered(true)}
          onMouseLeave={() => setHovered(false)}
        >
          {_trigger}
          <select
            className={bemCn('select', { size })}
            disabled={isDisabled ? true : undefined}
            onChange={handleChange}
            value={
              displayed.label !== '' ? displayed.value : placeholderOption.value
            }
          >
            {optionList}
          </select>
        </div>,
        { theme, size, error, label, note }
      )}
    </>
  );
}

const optionPropType = PropTypes.shape({
  label: PropTypes.string.isRequired,
  value: PropTypes.string
});
const noOptionsMessageConfigType = PropTypes.shape({
  noOptions: PropTypes.string.isRequired,
  loading: PropTypes.string
});
const noOptionsMessageType = PropTypes.oneOf([
  PropTypes.string,
  noOptionsMessageConfigType
]);

NativeSelect.propTypes = {
  className: PropTypes.string,
  options: PropTypes.arrayOf(optionPropType),
  defaultValue: optionPropType,
  value: optionPropType,
  onChange: PropTypes.func,
  size: PropTypes.oneOf(['m', 's', 'xs']),
  theme: PropTypes.oneOf(['light', 'dark']),
  placeholder: PropTypes.string,
  isDisabled: PropTypes.bool,
  isLoading: PropTypes.bool,
  trigger: PropTypes.func,
  error: PropTypes.oneOfType([PropTypes.bool, PropTypes.node]),
  label: PropTypes.node,
  note: PropTypes.node,
  noOptionsMessage: noOptionsMessageType
};

function getOptionValue(option) {
  return typeof option.value === 'string' ? option.value : option.label;
}

function isString(s) {
  return Object.prototype.toString.call(s) === '[object String]';
}

function containsGroups(opts) {
  return opts.length > 0 && opts[0].options && Array.isArray(opts[0].options);
}

function renderOptionGroup({ options = [], label = '' }, index) {
  return (
    <optgroup key={index} label={label}>
      {options.map(renderOption)}
    </optgroup>
  );
}

function renderOption(option) {
  const optionProps = {
    key: getOptionValue(option),
    value: getOptionValue(option)
  };

  if (option.disabled) {
    optionProps.disabled = true;
  }

  return (
    <option key={getOptionValue(option)} {...optionProps}>
      {option.label}
    </option>
  );
}

function findSelectedOption(options, selectedValue) {
  if (containsGroups(options)) {
    let result;
    for (const group of options) {
      result = group.options.find(o => getOptionValue(o) === selectedValue);
      if (result) return result;
    }
    return null;
  } else {
    return options.find(o => getOptionValue(o) === selectedValue);
  }
}

export default withTheme(NativeSelect);
