import React from 'react';
import PropTypes from 'prop-types';
import bemCnFast from 'bem-cn-fast';
import Button from '@webteam/button';

import {
  adaptPropsToShareButton,
  SOCIAL_COMPONENTS,
  SOCIAL_ICONS,
  SOCIAL_NETWORKS
} from '../utils';

const SocialButtons = ({
  className,
  theme,
  size,
  networks,
  options,
  clickHandler
}) => {
  const bemCn = bemCnFast(className);

  return (
    // eslint-disable-next-line react/jsx-no-useless-fragment
    <>
      {networks.map(network => {
        const SocialTagName = SOCIAL_COMPONENTS[network];
        const SocialIconTagName = SOCIAL_ICONS[network];
        return (
          <SocialTagName
            className={bemCn('item', { size })}
            {...adaptPropsToShareButton(network, options)}
            key={network}
          >
            <Button
              mode="nude"
              size={size}
              theme={theme}
              icon={<SocialIconTagName />}
              onClick={() => clickHandler(network, options.url)}
            />
          </SocialTagName>
        );
      })}
    </>
  );
};

SocialButtons.propTypes = {
  networks: PropTypes.arrayOf(PropTypes.oneOf(Object.values(SOCIAL_NETWORKS)))
    .isRequired,
  options: PropTypes.shape({
    hashtag: PropTypes.string,
    text: PropTypes.string.isRequired,
    url: PropTypes.string.isRequired,
    via: PropTypes.string
  }).isRequired,
  className: PropTypes.string,
  theme: PropTypes.oneOf(['light', 'dark']),
  size: PropTypes.oneOf(['m', 's', 'xs']),
  clickHandler: PropTypes.func.isRequired
};

export default SocialButtons;
